import React, { Component } from "react"
import { Link } from "gatsby"
import { connect } from "react-redux"
import weekly from "../components/wrapup.module.scss"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faArrowUp } from "@fortawesome/free-solid-svg-icons"
import { faStar } from "@fortawesome/free-solid-svg-icons"

import { faThumbsUp } from "@fortawesome/free-solid-svg-icons"
import Moment from "react-moment"
import { Doughnut, Bar, Line } from "react-chartjs-2"
import { HorizontalBar } from "react-chartjs-2"
import { Polar } from "react-chartjs-2"
import { Pie } from "react-chartjs-2"

class WrapUp extends Component {
  render() {
    const categories = this.props.categories.map((category, i) => {
      return (
        <>
          <div className={weekly.section}>
            <h1 className={weekly.categoryName}>{category.title}</h1>
          </div>

          {this.props.events.map((event, i) => {
            if (event.category === category.title) {
              return (
                <div className={weekly.eventContainer}>
                  <div className={weekly.eventHeader}>
                    <h2 className={weekly.eventName}>
                      <FontAwesomeIcon icon={faStar} className={weekly.star} />
                      {event.title}
                    </h2>
                  </div>
                  <div className={weekly.row}>
                    <div className={weekly.topGrid}>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>{event.count}</h3>
                        <h4 className={weekly.tileFontSmall}>
                          Current Weekly Count
                        </h4>
                      </div>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>{event.count}</h3>
                        <h4 className={weekly.tileFontSmall}>
                          Last Week Total Count
                        </h4>
                      </div>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>{event.count}</h3>
                        <h4 className={weekly.tileFontSmall}>Average Count</h4>
                      </div>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>
                          {event.positiveExpHistory}
                        </h3>
                        <h4 className={weekly.tileFontSmall}>
                          Positive Experiences
                        </h4>
                      </div>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>
                          {event.negativeExpHistory}
                        </h3>
                        <h4 className={weekly.tileFontSmall}>
                          Negative Experiences
                        </h4>
                      </div>
                      <div className={weekly.tile}>
                        <h3 className={weekly.tileFontLarge}>
                          {event.neutralExpHistory}
                        </h3>
                        <h4 className={weekly.tileFontSmall}>
                          Neutral Experiences
                        </h4>
                      </div>
                    </div>
                  </div>
                </div>
              )
            }
          })}
        </>
      )
    })
    return <div className="content">{categories}</div>
  }
}

function mapStateToProps(state) {
  return {
    categories: state.categories,
    events: state.events,
  }
}

export default connect(mapStateToProps)(WrapUp)

